.menu {
  &__nav {
    position: absolute;
    z-index: 3;
    top: 47px;
    right: 0px;
  }

  &__itemsList {
    position: relative;

    display: flex;
    flex-direction: column;

    min-width: 111px;
    padding: 5px 0;

    background: white;
    border-radius: 5px;

    animation: animateHeight 2s ease forwards;

    &::before {
      content: '';

      position: absolute;
      z-index: 0;
      top: -4px;
      right: 16px;
      transform: rotate(45deg);

      width: 10px;
      height: 10px;

      background: white;
      border-radius: 3px;
    }
  }

  &__item {
    cursor: pointer;

    transform: translate(0, -20px);

    display: flex;
    gap: 7px;
    align-items: center;

    padding: 10px 15px;

    color: black;
    white-space: nowrap;

    opacity: 0;

    transition: all 0.2s ease;
    animation: animateMenuOpen 0.3s ease forwards;

    &:hover {
      background: rgb(128 128 128 / 66.7%);
    }
  }
}
@keyframes animateMenuOpen {
  0% {
    transform: translate(0, -20px);

    opacity: 0;
  }

  100% {
    transform: translate(0, 0);

    opacity: 1;
  }
}
@keyframes animateHeight {
  0% {
    max-height: 0vh;
  }

  100% {
    max-height: 100vh;
  }
}
